import styled from 'styled-components';
import colors from 'theme/colors';

export const Wrapper = styled.div`
  min-height: 100vh;
  width: 100%;
  background: ${colors.navy};
  padding: 80px 60px 80px 250px;
  position: relative;
  color: ${colors.white};
  @media (max-width: 1200px) {
    padding: 80px 60px 80px 150px;
  }
  @media (max-width: 600px) {
    padding: 60px 25px;
  }
  .timeline {
    position: absolute;
    top: 0;
    left: 175px;
    width: 3px;
    height: 100%;
    background: ${colors.white};
    @media (max-width: 1200px) {
      left: 90px;
    }
    @media (max-width: 600px) {
      display: none;
    }

    .point {
      position: absolute;
      top: 97px;
      left: -10px;
      width: 23px;
      height: 23px;
      border-radius: 50%;
      border: 3px solid ${colors.white};
      display: block;
      background: ${colors.navy};
    }
  }
  > article {
    margin-bottom: 100px;
    @media (max-width: 600px) {
      margin-bottom: 60px;
    }
  }
  h3 {
    font-size: 48px;
    margin: 0 0 40px;
    @media (max-width: 1000px) {
      font-size: 40px;
    }
  }
  h4 {
    font-size: 28px;
    margin: 0 0 10px;
    @media (max-width: 1000px) {
      font-size: 21px;
    }
  }
`;

export const Project = styled.div`
  position: relative;
  margin-left: 47px;
  margin-bottom: 80px;
  @media (max-width: 1200px) {
    margin-left: 0;
  }
  @media (max-width: 600px) {
    margin-bottom: 40px;
  }

  &:before {
    width: 120px;
    height: 3px;
    background: ${colors.white};
    content: '';
    position: absolute;
    left: -120px;
    top: 15px;

    @media (max-width: 1200px) {
      width: 60px;
      left: -60px;
    }
    @media (max-width: 600px) {
      display: none;
    }
  }
  &:after {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    display: block;
    background: ${colors.white};
    position: absolute;
    content: '';
    top: 9px;
    left: -5px;
    @media (max-width: 600px) {
      display: none;
    }
  }
  > header {
    padding-left: 30px;
    max-width: 1000px;
    @media (max-width: 600px) {
      padding-left: 0;
    }
  }
  h5 {
    font-size: 28px;
    @media (max-width: 600px) {
      font-size: 24px;
    }
  }
  p {
    font-size: 21px;
    margin-top: 20px;
    line-height: 1.4;
    @media (max-width: 600px) {
      font-size: 18px;
    }

    strong {
      font-size: 18px;
      font-style: italic;
      font-weight: normal;
    }
  }
  small {
    font-size: 18px;
    display: block;
    margin: 10px 0 0;
    @media (max-width: 600px) {
      font-size: 16px;
    }
  }

  > figure {
    display: flex;
    align-items: center;
    margin: 30px;
    position: relative;
    width: 500px;
    height: 400px;
    cursor: pointer;
    @media (max-width: 600px) {
      margin: 40px 0 0;
      width: 100%;
      height: auto;
    }

    &:hover .thumb-project {
      filter: brightness(50%);
    }
    &:hover button {
      opacity: 1;
      visibility: visible;
    }
    .thumb-project {
      width: 100%;
      height: 100%;
      object-fit: contain;
      position: relative;
      transition: 0.3s all ease-in-out;
      box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
    }
    button {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      align-items: center;
      z-index: 100;
      border: none;
      opacity: 0;
      background: transparent;
      visibility: hidden;
      transition: 0.3s all ease-in-out;
      padding: 15px 30px;
      background: ${colors.navy};
      border-radius: 30px;
      min-width: 180px;
      text-align: center;
      span {
        font-size: 14px;
        text-transform: uppercase;
        font-family: 'Roboto Mono', monospace;
        color: ${colors.white};
      }
    }
    .icon-link {
      width: 15px;
      display: block;
      margin-right: 10px;
    }
  }
`;
