import styled from 'styled-components';
import colors from 'theme/colors';

export const Wrapper = styled.div`
  min-height: 100vh;
  width: 100%;
  background: ${colors.black};
  padding: 80px 60px 80px 250px;
  position: relative;
  color: ${colors.white};
  @media (max-width: 1200px) {
    padding: 80px 60px 80px 150px;
  }
  @media (max-width: 600px) {
    padding: 60px 25px;
  }
  .timeline {
    position: absolute;
    top: 0;
    left: 175px;
    width: 3px;
    height: 100%;
    background: ${colors.white};
    @media (max-width: 1200px) {
      left: 90px;
    }
    @media (max-width: 600px) {
      display: none;
    }

    .point {
      position: absolute;
      top: 97px;
      left: -10px;
      width: 23px;
      height: 23px;
      border-radius: 50%;
      border: 3px solid ${colors.white};
      display: block;
      background: ${colors.black};
    }
  }
  > article {
    margin-bottom: 100px;
    max-width: 800px;
    @media (max-width: 600px) {
      margin-bottom: 60px;
    }
  }
  h3 {
    font-size: 48px;
    margin: 0 0 40px;
    @media (max-width: 1000px) {
      font-size: 40px;
    }
  }
  h4 {
    font-size: 28px;
    margin: 0 0 10px;
    line-height: 1.4;
    @media (max-width: 1000px) {
      font-size: 21px;
    }
  }
`;

export const Code = styled.div`
  margin: 50px 0 0;
  font-family: 'Roboto Mono', monospace;
  max-width: 1000px;
  font-size: 16px;
  @media (max-width: 600px) {
    font-size: 12px;
  }

  .code-row {
    margin-bottom: 5px;
  }
  .code-dots {
    color: #3b3553;
  }
  .code-yellow {
    color: #dcdc8b;
  }
  .code-blue {
    color: #569cd6;
  }
  .code-skyblue {
    color: #7bdcfe;
  }
  .code-purple {
    color: #a586c0;
  }
  .code-green {
    color: #24e33a;
  }
  .code-brown {
    color: #ce915b;
  }
`;
