import React from 'react';
import thumbAwc from 'assets/thumb-awc.jpg';
import thumbStm from 'assets/thumb-stm.jpg';
import thumbCovid from 'assets/thumb-covid.jpg';
import iconLink from 'assets/icon-link.svg';

import * as S from './styles';

function Work() {
  return (
    <S.Wrapper id="work">
      <div className="timeline">
        <span className="point"></span>
      </div>
      <article>
        <h3 className="font-primary">Work</h3>
        <h4>My selected works over the years.</h4>
      </article>
      <S.Project>
        <header>
          <h5 className="font-primary">Affiliate World Conferences</h5>
          <p>
            Affiliate World is the premiere affiliate marketing conference for
            the world's top affiliate marketers, ecommerce marketing experts and
            industry authorities.
          </p>
          <p>
            <strong>Responsibilites:</strong>
          </p>
          <small>
            · Lead the front-end team in building elegant and sophisticated user
            interfaces.
          </small>
          <small>
            · Collaborates with designers, developers, and product owners in
            order to avail interactive and scalable web apps.
          </small>
          <small>
            · Communicate front-end best practices to the team, enhancing
            efficiency and performance across the department.
          </small>
          <small>
            · Mentored and knowledge-shared with younger colleagues.
          </small>
          <p>
            <strong>Tech:</strong>
          </p>
          <small>
            React, Redux, Vue, Flutter, Styled Components, Sass/Less
          </small>
        </header>
        <figure>
          <a
            href="https://affiliateworldconferences.com/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={thumbAwc} alt="" className="thumb-project" />
            <button>
              <img src={iconLink} alt="" className="icon-link" />
              <span>Visit Site</span>
            </button>
          </a>
        </figure>
      </S.Project>
      <S.Project>
        <header>
          <h5 className="font-primary">STM Forum</h5>
          <p>
            STM Forum is the #1 rated community for affiliate and performance
            internet marketers.
          </p>
          <p>
            <strong>Responsibilites:</strong>
          </p>
          <small>
            · Handle UI/UX development for sales page and lead gen websites.
          </small>
          <small>· Help maintain forum and user interface updates.</small>

          <p>
            <strong>Tech:</strong>
          </p>
          <small>
            React, Vue, jQuery, Styled Components, vBulletin
          </small>
        </header>
        <figure>
          <a
            href="https://stmforum.com/join/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={thumbStm} alt="" className="thumb-project" />
            <button>
              <img src={iconLink} alt="" className="icon-link" />
              <span>Visit Site</span>
            </button>
          </a>
        </figure>
      </S.Project>
      <S.Project>
        <header>
          <h5 className="font-primary">Covid19 Tracker</h5>
          <p>
            A prototype for tracking Coronavirus COVID19.
          </p>
          <p>
            <strong>Tech:</strong>
          </p>
          <small>
            React, Redux, Ant Design, Styled Components, Recharts
          </small>
        </header>
        <figure>
          <a
            href="https://voncarcha.github.io/covid19-tracker"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img src={thumbCovid} alt="" className="thumb-project" />
            <button>
              <img src={iconLink} alt="" className="icon-link" />
              <span>Visit Site</span>
            </button>
          </a>
        </figure>
      </S.Project>
      <S.Project>
        <header>
          <h5 className="font-primary">More Projects Coming Soon...</h5>
        </header>
      </S.Project>
    </S.Wrapper>
  );
}

export default Work;
