import styled, { keyframes } from 'styled-components';
import colors from 'theme/colors';
import bg from 'assets/bg-header.jpg';
import iconMouse from 'assets/icon-mouse.svg';

const logoColor = keyframes`
  from {
    fill: ${colors.black};
  }
  to {
    fill: ${colors.white};
  }
`;

const animate1 = keyframes`
  from {
    transform: rotateY(50deg);
    transform-origin: 0 0 0;
    opacity: 0;
    visibility: hidden;
  }
  to {
    transform: rotateY(0deg);
    transform-origin: 0 0 0;
    opacity: 1;
    visibility: visible;
  }
`;

const animate2 = keyframes`
  from {
    transform: rotateY(50deg);
    transform-origin: 100% 100% 0;
    opacity: 0;
    visibility: hidden;
  }
  to {
    transform: rotateY(0deg);
    transform-origin: 100% 100% 0;
    opacity: 1;
    visibility: visible;
  }
`;

const animate3 = (top, left) => keyframes`
  from {
    transform: translate3d(-50%, -50%, 0) scale(1);
    left: 50%;
    top: 50%;
    background: transparent;
  }
  to {
    transform: translate3d(0, 0, 0) scale(0.5);
    left: ${left};
    top: ${top};
    background: ${colors.black};
  }
`;

const animate4 = keyframes`
  from {
    transform: translate3d(100px, 0, 0);
    opacity: 0;
    visibility: hidden;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
  }
`;
const animate5 = keyframes`
  from {
    transform: translate3d(-100px, 0, 0);
    opacity: 0;
    visibility: hidden;
  }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
  }
`;

const animate6 = keyframes`
  from {
    opacity: 0;
    visibility: hidden;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
`;

const animate7 = keyframes`
  0% {
    height: 0;
  }
  100% {
    height: 85%;
  }
`;

export const Wrapper = styled.div`
  position: relative;
  height: 100vh;
  width: 100%;
  background: ${colors.white} url(${bg}) center left/cover no-repeat;
  display: flex;
  padding: 0 20px;
  .bg-header {
    height: 100%;
    object-fit: contain;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 0;
    opacity: 0;
    visibility: hidden;
    animation: ${animate4} 0.3s 3s ease-in-out forwards;
    @media (max-width: 1200px) {
      right: -50px;
    }
  }
  .slogan {
    align-self: center;
    margin: 50px 0 0 250px;
    animation: ${animate5} 0.3s 3s ease-in-out forwards;
    transform: translate3d(-100px, 0, 0);
    opacity: 0;
    visibility: hidden;
    @media (max-width: 1200px) {
      margin: 50px 0 0 150px;
    }
    @media (max-width: 900px) {
      margin: 50px 0 0 120px;
      padding: 40px;
      border-radius: 10px;
      background: rgba(255, 255, 255, 0.4);
    }
    @media (max-width: 600px) {
      margin: 50px auto 0;
      text-align: center;
      padding: 20px;
    }
  }
  h1 {
    margin: 0;
    font-weight: normal;
    font-size: 60px;
    color: ${colors.black};
    line-height: 1;
    @media (max-width: 1000px) {
      font-size: 45px;
    }
    @media (max-width: 600px) {
      font-size: 35px;
    }

    strong {
      font-size: 90px;
      font-weight: normal;
      font-family: 'Abril Fatface', cursive;
      @media (max-width: 1000px) {
        font-size: 60px;
      }
      @media (max-width: 600px) {
        font-size: 45px;
      }
    }
  }
  h2 {
    margin: 30px 0 0;
    font-weight: normal;
    font-size: 36px;
    color: ${colors.black};
    line-height: 1.4;
    @media (max-width: 1000px) {
      font-size: 26px;
    }
    @media (max-width: 600px) {
      margin: 20px 0 0;
      font-size: 18px;
    }
  }
  .timeline {
    position: absolute;
    top: 15%;
    left: 175px;
    width: 3px;
    height: 0;
    background: ${colors.black};
    animation: ${animate7} 0.5s 3s ease-in-out forwards;
    @media (max-width: 1200px) {
      left: 90px;
    }
    @media (max-width: 600px) {
      display: none;
    }

    &:before {
      position: absolute;
      bottom: 20%;
      left: -18px;
      content: '';
      width: 40px;
      height: 50px;
      opacity: 0;
      visibility: hidden;
      background: #f2f2f2 url(${iconMouse}) center/contain no-repeat;
      animation: ${animate6} 0.3s 3.5s ease-in-out forwards;
    }
  }
`;

export const Logo = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0) scale(1);
  transform-origin: center;
  z-index: 10;
  border-radius: 15px;
  animation: ${animate3('0', '0')} 0.5s 2.2s ease-in-out forwards;
  @media (max-width: 1200px) {
    animation: ${animate3('-25px', '-35px')} 0.5s 2.2s ease-in-out forwards;
  }
  svg {
    width: 350px;
    height: 300px;
    position: relative;
    @media (max-width: 1200px) {
      width: 250px;
      height: 200px;
    }

    .line-1 {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      animation: ${animate1} 0.5s 0.4s ease-in-out forwards,
        ${logoColor} 0.3s 2.2s linear forwards;
    }
    .line-2 {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      animation: ${animate1} 0.5s 0.8s ease-in-out forwards,
        ${logoColor} 0.3s 2.2s linear forwards;
    }
    .line-3 {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      animation: ${animate2} 0.5s 1.2s ease-in-out forwards,
        ${logoColor} 0.3s 2.2s linear forwards;
    }
    .line-4 {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      animation: ${animate2} 0.5s 1.6s ease-in-out forwards,
        ${logoColor} 0.3s 2.2s linear forwards;
    }
  }
`;
