import styled from 'styled-components';
import colors from 'theme/colors';

export const Wrapper = styled.div`
  width: 100%;
  background: ${colors.white};
  padding: 80px 60px 80px 250px;
  position: relative;
  color: ${colors.black};
  @media (max-width: 1200px) {
    padding: 80px 60px 80px 150px;
  }
  @media (max-width: 600px) {
    padding: 60px 25px;
  }
  .timeline {
    position: absolute;
    top: 0;
    left: 175px;
    width: 3px;
    height: 100%;
    background: ${colors.black};
    @media (max-width: 1200px) {
      left: 90px;
    }
    @media (max-width: 600px) {
      display: none;
    }

    .point {
      position: absolute;
      top: 97px;
      left: -10px;
      width: 23px;
      height: 23px;
      border-radius: 50%;
      border: 3px solid ${colors.black};
      display: block;
      background: ${colors.white};
    }
  }
  h3 {
    font-size: 48px;
    margin: 0 0 40px;
    @media (max-width: 1000px) {
      font-size: 40px;
    }
  }
  h4 {
    font-size: 28px;
    @media (max-width: 1000px) {
      font-size: 21px;
    }
  }
  a {
    color: #569cd6;
    &:hover {
      text-decoration: underline;
    }
  }
`;
